/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */

.text-img { --container-width: 1320px; }
.text-img p { color: #626160; }
.text-img .row { align-items: center; }
.text-img .col-md-6 { display: flex; flex-direction: column; }
.text-img .content-wrap { max-width: 400px; margin: 0 auto; padding: 2rem 0; }
.text-img .img-holder { position: relative; }
.text-img .img-holder a { position: absolute; top: calc(50% - 2rem); left: calc(50% - 2rem); width: 4rem; height: 4rem; font-size: 1.5rem; background: var(--purple); color: #fff; display: flex; align-items: center; justify-content: center; border-radius: 50%; text-decoration: none; transition: transform 1s cubic-bezier(0, 0, 0, 1) 0s; }

.text-img-small { padding: 3rem 0; }

.text-img-large {  }
.text-img-large .col-md-6:nth-child(1) .img-container { margin-left: -0.75rem; }
.text-img-large .col-md-6:nth-child(2) .img-container { margin-right: -0.75rem; }
.text-img-large .img-wrap { background: #F9E3EC; display: flex; justify-content: center; align-items: center; padding: 5rem; }

.text-img-featured { min-height: 100vh; display: flex; flex-direction: column; padding-top: 5rem; padding-bottom: 5rem; }
.text-img-featured .row { flex-grow: 1; }
.text-img-featured img { display: block; height: auto; width: auto; max-height: 500px; position: relative; }
.text-img-featured img::after { content: 'aa'; }
.text-img-featured h3 { font-size: 2.25rem; color: var(--purple); font-family: 'Inter'; font-weight: 300; }
.text-img-featured p { font-family: 'Newsreader'; font-weight: 300; font-size: 1.875rem; line-height: 1.6; }
.text-img-featured .content-wrap { margin: 0; max-width: none; }
.text-img-featured .col-md-6:nth-child(1) { max-width: calc(var(--container-width) / 2); margin-left: auto; }
.text-img-featured .col-md-6:nth-child(1)::after { content: ''; z-index: -1; position: absolute; width: 22%; height: 100%; left: 0; top: 0; background: var(--peach); display: none; }
.text-img-featured .col-md-6:nth-child(1) .img-container { margin-right: auto; margin-left: 25%; }
.text-img-featured .col-md-6:nth-child(2) {  }
.text-img-featured .col-md-6:nth-child(2) .img-container { margin-left: auto; margin-right: 25%; }
.text-img-featured .col-md-6:nth-child(2)::after { content: ''; z-index: -1; position: absolute; width: 22%; height: 100%; right: 0; top: 0; background: var(--peach); }

.text-img-featured.img-text .col-md-6:nth-child(1)::after { display: block; }
.text-img-featured.img-text .col-md-6:nth-child(2)::after { display: none; }

.text-img-featured.img-text .col-md-6:nth-child(1) { max-width: none; margin-left: 0; }
.text-img-featured.img-text .col-md-6:nth-child(2) { max-width: calc(var(--container-width) / 2); margin-right: auto; }

@media only screen and (max-width: 1399px) {
   .text-img { --container-width: 1140px; }

   .text-img-small { padding: 2.75rem 0; }

   .text-img-large .col-md-6:nth-child(1),
   .text-img-large.img-text .col-md-6:nth-child(2) { max-width: var(--container-width); margin-left: auto; margin-right: auto; }
   .text-img-large.img-text .col-md-6:nth-child(1) { max-width: none; }
   .text-img-large .img-wrap { padding: 4.5rem; }

   .text-img-featured { padding-top: 4.75rem; padding-bottom: 4.75rem; }
   .text-img-featured h3 { font-size: 2rem; }
   .text-img-featured p { font-size: 1.75rem; }

   .text-img-featured .col-md-6:nth-child(1) .img-container { margin-left: 20%; }
   .text-img-featured .col-md-6:nth-child(2) .img-container { margin-right: 20%; }
   .text-img-featured .col-md-6:nth-child(1)::after,
   .text-img-featured .col-md-6:nth-child(2)::after { width: 20%; }
}

@media only screen and (max-width: 1199px) {
   .text-img { --container-width: 960px; }

   .text-img-small { padding: 2.25rem 0; }

   .text-img-large .img-wrap { padding: 4rem; }

   .text-img-featured { padding-top: 4.25rem; padding-bottom: 4.25rem; }
   .text-img-featured h3 { font-size: 1.875rem; }
   .text-img-featured p { font-size: 1.5rem; }

   .text-img-featured .col-md-6:nth-child(1) .img-container { margin-left: 17%; }
   .text-img-featured .col-md-6:nth-child(2) .img-container { margin-right: 17%; }
   .text-img-featured .col-md-6:nth-child(1)::after,
   .text-img-featured .col-md-6:nth-child(2)::after { width: 17%; }
}

@media only screen and (max-width: 991px) {
   .text-img { --container-width: 720px; }

   .text-img-small { padding: 2rem 0; }

   .text-img-large .img-wrap { padding: 3.5rem; }

   .text-img-featured { padding-top: 3.75rem; padding-bottom: 3.75rem; }
   .text-img-featured h3 { font-size: 1.75rem; }
   .text-img-featured p { font-size: 1.25rem; }
}


@media only screen and (max-width: 767px) {
   .text-img { --container-width: 540px; }
   .text-img .content-wrap { max-width: none; }
   .text-img.img-text .row { flex-direction: column-reverse; }

   .text-img-small { padding: 0; margin-bottom: 1.5rem; }

   .text-img-large .col-md-6:nth-child(1) .img-container { margin-right: -0.75rem; }
   .text-img-large .col-md-6:nth-child(2) .img-container { margin-left: -0.75rem; }
   .text-img-large .img-wrap { padding: 3rem; }

   .text-img-featured { min-height: 0; padding-top: 3.25rem; padding-bottom: 3.25rem; }
   .text-img-featured .col-md-6:nth-child(1),
   .text-img-featured .col-md-6:nth-child(2),
   .text-img-featured.img-text .col-md-6:nth-child(1),
   .text-img-featured.img-text .col-md-6:nth-child(2) { margin-right: auto; margin-left: auto; max-width: var(--container-width); }

   .text-img-featured .col-md-6:nth-child(1) .img-container { margin-left: auto; }
   .text-img-featured .col-md-6:nth-child(2) .img-container { margin-right: auto; }

   .text-img-featured .col-md-6:nth-child(1)::after,
   .text-img-featured .col-md-6:nth-child(2)::after { width: 100%; height: 10rem; bottom: 0; top: auto; }
}

@media only screen and (max-width: 575px) {
   .text-img { --container-width: 100%; }
   .text-img-featured { padding-top: 2.75rem; padding-bottom: 2.75rem; }

   .text-img-featured .col-md-6:nth-child(1)::after,
   .text-img-featured .col-md-6:nth-child(2)::after { height: 6rem; }
}